const util = require("../utils/index.js");

const constant = require("../constant/index.js");

class LoginFactory {
	constructor() {
		this.coreUtil = util.core;
	} //  storage 


	checkLoginStatus(callback = () => {}) {
		let _that = this;

		let loginFlag = wx.getStorageSync(constant.mapping.LOGIN_FLAG);
		
		let nowDate = new Date();

















		if (loginFlag && loginFlag.expireTime > nowDate.getTime()) {
			//  session_key 
			wx.checkSession({
				// session_key ()
				success: function() {
					console.log('');
					callback();
				},
				// session_key 
				fail: function() {
					// session_key
					_that.doLogin();
				}
			});
		} else {
			// 
			_that.doLogin(callback);
		}

	} // 


	doLogin(callback = () => {}) {
		let that = this;
		wx.login({
			success: function(loginRes) {
				if (loginRes.code) {
					// TODO
					//
					that.requsetHcServerToLogin(loginRes, callback);
				} else {
					//  code 
					that.coreUtil.showInfo('');
					console.log('wx.logincode');
				}
			},
			fail: function(error) {
				//  wx.login 
				that.coreUtil.showInfo('' + error);
				console.log(error);
			}
		});
	}
	/**
	 *  HC 
	 */


	requsetHcServerToLogin(loginRes, callback = () => {}) {
		let defaultRawData = '{"nickName":"","gender":1,"language":"","city":"","province":"","country":"","avatarUrl":""}'; // 
		console.log('', loginRes);
		wx.request({
			url: constant.url.loginUrl,
			method: 'post',
			header: {
				'APP-ID': constant.app.appId
			},
			data: {
				code: loginRes.code,
				// 
				userInfo: JSON.parse(defaultRawData),
				// 
				signature: '',
				// 
				encryptedData: '',
				// 
				iv: '' // 

			},
			success: function(res) {
				console.log('login success123...:', res);

				if (res.statusCode == '401') {
					let data = res.data;
					uni.setStorageSync(constant.mapping.CURRENT_OPEN_ID, data.openId);
					wx.reLaunch({
						url: '/pages/login/login'
					});
					return;
				}

				res = res.data;

				if (res.result == 0) {
					//that.globalData.userInfo = res.userInfo;
					console.log(res.userInfo);
					wx.setStorageSync(constant.mapping.USER_INFO, JSON.stringify(res.userInfo));
					let date = new Date();
					let year = date.getFullYear(); //

					let mon = date.getMonth(); //

					let da = date.getDate(); //

					let h = date.getHours() + 1; //

					let m = date.getMinutes(); //

					let s = date.getSeconds(); //

					console.log("", year, mon, da, h, m, s); //

					let afterOneHourDate = new Date(year, mon, da, h, m, s); //30s

					console.log("afterOneHourDate", afterOneHourDate);
					wx.setStorageSync(constant.mapping.LOGIN_FLAG, {
						sessionKey: res.sessionKey,
						expireTime: afterOneHourDate.getTime()
					});
					wx.setStorageSync(constant.mapping.TOKEN, res.token);
					callback();
				} else {
					util.core.showInfo(res.errmsg);
				}
			},
			fail: function(error) {
				// 
				if (error.statusCode == 401) {
					uni.reLaunch({
						url: '/pages/login/login'
					});
					return;
				}
				util.core.showInfo('');
				console.log(error);
			}
		});
	} //  


	getLoginFlag() {
		return wx.getStorageSync(constant.mapping.LOGIN_FLAG);
	}

}

;
module.exports = new LoginFactory();
